.postsBlock {
    grid-area: p;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    margin: 0 0 20px 0;
    padding: 15px 0 0 0;
    height: 95%;
    background-color: var(--primary-bg-color);
    border-radius: 20px;
}

.feedTitle {
    margin-left: 15px;
    display: flex;
    align-items: center;
    align-self: start;
    width: 120px;
    height: 28px;
    font-weight: 300;
    border-radius: 10px;
    box-shadow: 1px 1px 3px var(--primary-decoration-color);
    border: 1.5px solid #1e8449;
}

.feedTitle>h4 {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.posts {
    padding-top: 10px;
}

.newMessage {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: space-around;
    width: fit-content;
    min-height: fit-content;
    height: 150px;
    margin-top: 15px;
    padding: 0px 20px;
    position: relative;
    z-index: 1;
    overflow: hidden;
    -webkit-box-shadow: 0 0 2px 1px black;
    -moz-box-shadow: 0 0 2px 1px black;
    box-shadow: 0 0 2px 1px black;
}

.newMessage button {
    margin-top: -15px;
}

.newMessage textarea {
    padding: 25px 0px 0 8px;
    width: 240px;
    height: 70px;
    resize: none;
}

.newMessageBg {
    width: 240px;
    height: 125px;
    top: 18px;
    left: 35px;
    position: absolute;
    background-color: var(--secondary-bg-color);
    z-index: -1;
}

.error {
    margin: 0 0 10px 0;
    padding: 3px 4px;
    border: 1px dashed red;
    color: var(--primary-text-color);
    font-weight: 700;
    font-size: 16px;
}

@media (max-width: 1200px) {
    .newMessageBg {
        width: 200px;
    }

    .newMessage textarea {
        width: 200px;
    }
}

@media (max-width: 640px) {
    .newMessage {
        height: 120px;
    }

    .newMessageBg {
        width: 100px;
    }

    .newMessage textarea {
        padding: 0;
        height: 60px;
        width: 100px;
    }

    .feedTitle {
        width: 80px;
    }
}